import { pipe } from 'it-pipe'
import { fromString as uint8ArrayFromString } from 'uint8arrays/from-string'

const AuthMessage = "<Auth></Auth>";
const ListMessage = "<List><Action>all</Action></List>";
const StopMessage = "<Stop><UniqueId>None</UniqueId></Stop>";
const HelloMessage = "<Hello></Hello>";
const StatusMessage = "<Status><UniqueId>None</UniqueId></Status>"


async function sendMessageToPeer(libp2pNode, bootstrapAddr, message) {
  if (!libp2pNode) {
    console.error('Libp2p node not ready')
    return
  }

  try {
    const connection = await libp2pNode.components.connectionManager.openConnection(bootstrapAddr)
    const stream  = await connection.newStream(['/c9r/0.0.1']);
 
    await pipe(
      [uint8ArrayFromString(message + '\n')], // : \n  Go-
      stream.sink
    )

  
    const decoder = new TextDecoder()
    for await (const chunk of stream.source) {
      //  chunk  Uint8Array
      const data = chunk.subarray ? chunk.subarray() : chunk
      const response = decoder.decode(data);
      return response;
    }


  } catch (err) {
    console.error(' Failed to send message:', err)
  }
}



function StartMessage(podHash) {
    const ListMessage = `<Start><Hash>${podHash}</Hash><UniqueId>None</UniqueId><Time>0</Time></Start>`;
    return ListMessage;
}





export async function GetStatus(libp2pNode, bootstrapAddr) {
    return await sendMessageToPeer(libp2pNode, bootstrapAddr, StatusMessage);
}

export async function GetList(libp2pNode, bootstrapAddr) {
    return await sendMessageToPeer(libp2pNode, bootstrapAddr, ListMessage);
}

export async function RunPod(libp2pNode, bootstrapAddr, podHash) {
    const msg = StartMessage(podHash);
    return await sendMessageToPeer(libp2pNode, bootstrapAddr, msg);
}

export async function StopPod(libp2pNode, bootstrapAddr) {
    return await sendMessageToPeer(libp2pNode, bootstrapAddr, StopMessage);
}

export async function Hello(libp2pNode, bootstrapAddr) {
  return await sendMessageToPeer(libp2pNode, bootstrapAddr, HelloMessage);
}

