import React, { useEffect, useState } from 'react'
import { createLibp2p } from 'libp2p'
import { webSockets } from '@libp2p/websockets'
import { mplex } from '@libp2p/mplex'
import { yamux } from '@chainsafe/libp2p-yamux'
import { noise } from '@chainsafe/libp2p-noise'
import { kadDHT, removePrivateAddressesMapper } from '@libp2p/kad-dht'
import { multiaddr } from '@multiformats/multiaddr'
import { bootstrap } from '@libp2p/bootstrap'
import { ping } from '@libp2p/ping'
import { identify } from '@libp2p/identify'
import { keys } from '@libp2p/crypto';
import {GetList, Hello} from './requests';
//import { fromString as uint8ArrayFromString } from 'uint8arrays/from-string'




async function importRsaPrivateKeyFromBase64(base64Key) {
  const keyBytes = Uint8Array.from(atob(base64Key), c => c.charCodeAt(0));
  const privateKey =  keys.privateKeyFromProtobuf(keyBytes);
  return privateKey;
}


function App() {
  const [libp2pNode, setLibp2pNode] = useState(null);
  const [peerAddr, setPeerAddr] = useState(null);
  const [response, setResponse] = useState(''); //    
  const [isConnecting, setIsConnecting] = useState(false);
  

  const bootstrapStr = '/ip4/192.168.88.196/tcp/8888/ws/p2p/QmNoLG9oVTcQnAkjnMSYM7bPVRPcKkaEfd1wFJ3J4RVrrm';
  const bootstrapAddr = multiaddr(bootstrapStr);

  // These keys are used for testing purposes.
  const peerIdJson = {
    privKey: "", // base64 encoded
    pubKey: ""
  }
 
  useEffect(() => {
    (async () => {
      try {
        setIsConnecting(true);
        const privateKey = await importRsaPrivateKeyFromBase64(peerIdJson.privKey);
      


        const node = await createLibp2p({
          privateKey: privateKey, 
          peerDiscovery: [
            bootstrap({
              list: [bootstrapStr],  // list of known bootstrap peers
              interval: 60e3, // reconnect every 60 seconds
              timeout: 10e3, // wait for a response for up to 10 seconds
              tagName: 'bootstrap', // tag for internal libp2p tags
              tagValue: 50, // connection priority
              tagTTL: Infinity // the tag lives infinitely
            })
          ],
          transports: [webSockets()], // which channels can be connected (web socket)
          streamMuxers: [mplex(), yamux()], // Allow you to maintain multiple logical streams within a single physical connection.
          connectionEncrypters: [noise()], // Connection encryption
          // Services running on top of libp2p
          services: {
            // Implementation of Kademlia DHT  a distributed hash table.
            aminoDHT: kadDHT({
              protocol: '/ipfs/kad/1.0.0',
              peerInfoMapper: removePrivateAddressesMapper
            }),
            // Pings other peers to check latency and availability.
            ping: ping(),
            // Allows you to find out information about peers (versions, protocols, addresses).
            identify: identify()
          }
        })

        await node.start();
        console.log(' Node started:', node.peerId.toString());

        setLibp2pNode(node);

        try {
          await node.dial(bootstrapAddr)
          console.log(' Connection established')
          setIsConnecting(false);
        } catch (err) {
          console.error(' Connection error:', err)
        }

        node.handle('/c9r/0.0.1', async ({ stream }) => {
          const decoder = new TextDecoder()
          const reader = stream.getReader()
          while (true) {
            const { done, value } = await reader.read()
            if (done) break
            console.log(' Received message:', decoder.decode(value))
          }
        })

        node.addEventListener('peer:discovery', (evt) => {
          console.log('Found peer: ', evt.detail.toString())
        })

        node.addEventListener('peer:disconnect', (evt) => {
          console.log(' Connection to the feast lost:', evt.detail.toString())
        })

        

      } catch (err) {
        console.error(' Connection failed:', err)
        setIsConnecting(true)
      }
    })()
  }, [])





  const handleGetList = async () => {
    const resp = await Hello(libp2pNode, bootstrapAddr)
    if (resp !== null) {
      setResponse(resp)
    } else {
      setResponse('Error')
    }
  }

  return (
    <div>
      {!isConnecting && (
        <>
          <h1>Libp2p browser client</h1>
          <button onClick={handleGetList}>
           Test message
          </button>


          <div style={{ whiteSpace: 'pre-wrap', marginTop: '1em' }}>
            <strong>Response:</strong>
            <br />
            {response}
          </div>
        </>
      )}
    </div>
  )
}

export default App
